import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import Button from '../../../components/Button/Button';
import Textarea from '../../../components/Textarea/Textarea';
import Icon from '../../../components/Icon/Icon';
import * as code from './docs.js';
import DemoWrap from '../../components/DemoWrap/DemoWrap';

class Docs extends Component {
  constructor(props) {
    super(props)
    this.state = {
      productType: 2,
    }
  }
  productTypeChange = (val) => {
    this.setState({
      productType: val.value
    })
  }
  render() {
    return (
      <div className="markdown">
        <ReactMarkdown source={code.desc} />
        <h2></h2>
        <DemoWrap desc="" code={code.base}>
          <div>
            <Textarea />
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.placeholder}>
          <div>
            <Textarea placeholder=""/>
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.value}>
          <div>
            <Textarea value="value"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.size}>
          <div>
            <Textarea placeholder="sizesmall" size="small"/>
            <Textarea placeholder="sizebase" size="base"/>
            <Textarea placeholder="sizelarge" size="large"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.width}>
          <div>
            <Textarea placeholder="1000px" width="1000px"/>
            <Textarea placeholder="1000px"height="1000px"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.fontSize}>
          <div>
            <Textarea placeholder="20px" fontSize="20px"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={code.length}>
          <div>
            <Textarea placeholder="30" maxLength={30}/>
          </div>
        </DemoWrap>
        <ReactMarkdown source={code.api} />
      </div>
    );
  }
}

export default Docs;



